<template>
  <div>
    <b-form-group>
      <b-input-group>
        <b-form-input type="search" placeholder="Search..." v-model="searchText" autofocus debounce="300" />
        <b-input-group-append>
          <b-button>
            <b-icon icon="search"></b-icon>
          </b-button>
        </b-input-group-append>
      </b-input-group>
    </b-form-group>
    <b-table striped hover small :items="products" :fields="fields" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc">
      <template v-slot:cell(name)="{ item, value }">
        <router-link v-bind:to="{ path: `/${productType}/${item.productId}` }">{{ value }}</router-link>
      </template>
    </b-table>
  </div>
</template>

<script>
export default {
  props: {
    productType: String,
  },
  computed: {
    products() {
      let fields = this.fields.map((x) => x.key);
      return this.items.filter((x) => {
        for (let prop in fields) {
          let field = fields[prop];
          let exists = `${x[field]}`.toLowerCase()
            .includes(this.searchText.toLowerCase());
          if (exists) return exists;
        }
        return false;
      });
    },
  },
  async mounted() {
    await this.load();
  },
  methods: {
    async load() {
      try {
        let query = { productType: "standalone" };
        let url = `BackendServices/Products`;
        let response = await this.$http.get(url, { query: query });
        this.items = response.result;
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "name", sortable: true },
        { key: "serviceTypeName", sortable: true, label: "Service Type" },
        { key: "serviceCategoryName", sortable: true, label: "Service Category" },
        { key: "priceOrder", sortable: true },
      ],
      sortBy: "name",
      sortDesc: false,
      searchText: "",
    };
  },
};
</script>
